@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap);
* {
  box-sizing: border-box;
}

body {
  font-family: 'Roboto', sans-serif;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  margin: 0;
}

/* ------- WHEN SIGN FORM IS DISPLAYED  */

#main-sign-container {
  display: flex;
  flex-direction: column;
  width: 80vw;
  height: 100vh;
  align-items: center;
}

#main-sign-container h1 {
  text-align: center;
  font-size: 15vw;
}

#main-sign-container h3 {
  display: block;
}

.buttons-container {
  display: flex;
  width: 50%;
  flex-wrap: nowrap;
  justify-content: space-around;
}

#main-forms-container {
  margin-top: 5vh;
}

#main-forms-container .submit-button {
  margin-top: 1vh;
}
/* WHEN USER IS LOGGED IN */

#main-logged-notification {
  width: 100vw;
  text-align: center;
}

#header-container p {
  display: inline-block;
  margin-right: 5%;
  align-items: center;
}

#header-container {
  padding: 1em;
  margin-top: 3em;
  width: 100vw;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

#dashboard-container {
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  padding: 1em;
}

#activity-form {
  flex: 1 1;
  min-width: 420px;
  max-width: 720px;
  height: 80%;
  padding: 3em;
  background-color: #e8e8e8;
  border-radius: 1em;
}

#start-adress-label,
#end-adress-label,
#datepicker,
#datepicker {
  margin-top: 1em;
}

#statistics-container {
  flex: 1 1;
  min-width: 420px;
  max-width: 720px;
  height: 80%;
  border-radius: 1em;
  padding: 3em;
  overflow: scroll;
  display: flex;
  align-items: center;
  flex-direction: column;
}

#statistics-table {
  margin-top: 1em;
}

